import { Vote } from "./Vote";
import { Timestamp } from "./Timestamp"

import s from "../style/post.module.sass";
import { useContext, useState } from "react";
import { ConfirmMenuItem } from "./ConfirmMenuItem";
import { FaBan, FaCommentDots, FaCopy, FaEye, FaEyeSlash, FaLock, FaLockOpen, FaReply } from "react-icons/fa";
import { MenuData } from "./layout/ContextMenu";
import { useIdentity } from "../hooks/identity";
import {useEntity} from "../hooks/entity";
import { MdEdit } from "react-icons/md";
import { DropdownItem } from "./MultilevelMenu";
import { PostForm } from "./PostForm";

export function Comment(props) {
    var c = props.comment;
    var [editing, setEditing] = useState(false);
    var [replying, setReplying] = useState(false);
    var [deletion, setDeletion] = useState(false);

    var contextMenu = useContext(MenuData);
    var [id, axios] = useIdentity();
    var [comState, entity] = useEntity(c.entity);

    var indents = [];
    for(let i=0; i<c.level; i++) {
        indents.push(<Threadindent key={i}
            data-state={(c.hidden_lvl && c.hidden_lvl.includes(i))? "hidden" : (c.locked_lvl && c.locked_lvl.includes(i)) ? "locked" : undefined  }/>)
    }

    console.log(c);

    function context(evt) {
        if (deletion || editing) return false
        evt.preventDefault();

        var menu = [
            <ConfirmMenuItem text="Copy markdown" leftIcon={<FaCopy/>} onClick={async () => {
                try {
                    await navigator.clipboard.writeText(c.text)
                    return true;
                } catch (error) {
                    return false;
                }
            }} />
        ]
        if (typeof id.active !== "number" || !id.users[id.active])
            return contextMenu.openMenu(evt.pageX, evt.pageY, menu);
        
        if( c.locked_lvl.length === 0 ) 
            menu = [<DropdownItem text="Reply" onClick={reply} leftIcon={<FaReply />} />].concat(menu);

        if (c.author && id.users[id.active].username === c.author.username && props.onEdit)
            menu.push(
                <DropdownItem text="Edit" onClick={edit} leftIcon={<MdEdit />} />);
        /*if ((c.author && id.users[id.active].username === c.author.username) || id.users[id.active].moderator)
            menu.push(<DropdownItem text="Delete" onClick={deleteModal} warn={true} leftIcon={<FaBan />} />); */
        if (id.users[id.active].moderator) {
            var l = comState==="locked" || comState==="hidden";
            var h =  comState==="hidden";
            menu.push(
                <DropdownItem text="Comments" warn={true} leftIcon={<FaCommentDots />}>
                    <DropdownItem text={!l ? "Lock" : "Unlock"} leftIcon={!l ? <FaLock /> : <FaLockOpen />} warn={true} onClick={
                        async () => {let state = await entity(!l); contextMenu.removeMenu(); if(props.onComStateChange) props.onComStateChange(state);
                    }} />
                    <DropdownItem text={!h ? "Hide" : "Show"} leftIcon={!h ? <FaEyeSlash /> : <FaEye />} warn={true} onClick={
                        async() => {let state = await entity(undefined, !h); contextMenu.removeMenu(); if(props.onComStateChange) props.onComStateChange(state);
                    }} />
                </DropdownItem>
            )
        }

        contextMenu.openMenu(evt.pageX, evt.pageY, menu);
    }

    function edit () {
        setEditing(true);
        contextMenu.removeMenu();
    }
    function reply () {
        setReplying(true);
        contextMenu.removeMenu();
    }

    let hideIndicator = (
        <div
            className={s.comStateInd}
            data-state={comState}
            style={{display:"inline-block", marginLeft: "1em"}}
        >
            {comState==="hidden"? 
            <> Hidden <FaEyeSlash/></>:
             (comState==="locked"? 
             <> Locked <FaLock/></>:
             undefined)
            }
        </div>
    )

    if(editing) {
        return (
            <div className={s.commentWrap}>
                <div className={s.indents}>{indents}</div>
                <div className={s.comment}>
                    <PostForm comment={c.entity.id} cta="Edit!" edit text={c.text} onEdit={async (text, cancel) => {
                        if (cancel) return setEditing(false);

                        try {
                            var res = await axios.put(`/comment/${c.entity.id}`, { text });
                        } catch (error) {
                            console.error(error);
                            return false
                        }
                        console.log(res);
                        props.onEdit({...c, text});
                        setEditing(false);
                        return true;
                    }}></PostForm>
                </div>
            </div>
        );
    }

    if(!c.author && c.text === "~d~" || c.text === "~r~") {
        return (
            <div className={s.commentWrap}>
                <div className={s.indents}>{indents}</div>
                <div className={s.comment}>
                    <div className={s.indentVote}>
                        <Threadindent data-state={comState}></Threadindent>
                    </div>
                    <div className={s.content}>
                        <div>
                            <p className={s.author}>
                                <Timestamp time={c.createdAt} edit={c.updatedAt} compact={true}></Timestamp> {hideIndicator}
                            </p>
                        </div>
                        <div className={s.body}>
                            <p style={{color:"grey"}}>
                                COMMENT { c.text==="~d~" ? "‚ùåDELETED‚ùå" : "üî•REMOVEDüî•"}
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    return (
        <div className={s.commentWrap} onContextMenu={context}>
            <div className={s.indents}>{indents}</div>
            <div className={s.comment}>
                <div className={s.indentVote}>
                    <Vote up={c.entity.upvotes} down={c.entity.downvotes} small={true} id={c.entity.id}></Vote>
                    <Threadindent data-state={comState}></Threadindent>
                </div>
                <div className={s.content}>
                    <div>
                        <p className={s.author}>
                            posted by <span>{c.author ? c.author.username : "‚ùåDELETED‚ùå"}</span> <Timestamp time={c.createdAt} edit={c.updatedAt} compact={true}></Timestamp> {hideIndicator}
                        </p>
                    </div>
                    <div className={s.body}>
                        <p>
                            {c.text}
                        </p>
                        {replying && (
                            <PostForm comment={c.entity.id} cta="Reply!" edit onEdit={async (text, cancel) => {
                                if (cancel) return setReplying(false);
                                try {
                                    var res = await axios.post(`/comment/${c.entity.id}`, { text });
                                } catch (error) {
                                    console.error(error);
                                    return false
                                }
                                props.onReply({...c, text});
                                setEditing(false);
                                return true;
                            }}></PostForm>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
}

export function Threadindent(props) {
    return (
        <div className={s.threadindent} {...props}></div>
    );
}